.loginPageMainWrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  justify-content: flex-start;
  align-items: center;
  padding: 10px;
}

.loginLogoContainer {
  width: 15%;
  height: auto;
}

.loginLogoContainer img {
  width: 100%;
  height: auto;
}

.loginInputContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

.loginInputContainer input {
  width: 30%;
  height: 50px;
  border-style: solid;
  border-radius: 20px;
  margin: 20px;
  padding-left: 20px;
}

.loginInputContainer button {
  width: 200px;
  height: 50px;
  border-style: solid;
  border-radius: 20px;
  margin: 10px;
  background-color: var(--cafeOscuro);
  color: var(--blanco);
  font-weight: var(--bold);
}

.loginInputContainer button:hover {
  background-color: var(--blanco);
  color: var(--cafeOscuro);
}

@media (max-width: 760px) {
  .loginInputContainer input {
    width: 90%;
  }
}
